import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService, CommonsService, PopoverCardComponent, DynamicFormMobilePage } from 'ngxi4-dynamic-service';
import { MainService } from 'src/app/services/main.service';

import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { IonTextarea } from '@ionic/angular';

@Component({
  selector: 'app-idea-detail',
  templateUrl: './idea-detail.page.html',
  styleUrls: ['./idea-detail.page.scss'],
})
export class IdeaDetailPage implements OnInit {

  // khai bo bin  focus vo  nhp liu
  @ViewChild('textComment', { static: false }) textAreaElement: IonTextarea;
  userInfo: any;
  ideaInfo: any;
  message: string;

  uploadingFiles: any = [];
  isMobile: boolean = false;

  constructor(
    private route: ActivatedRoute
    , private apiCommons: CommonsService
    , private apiAuth: AuthService
    , private mainService: MainService
    , private iab: InAppBrowser
  ) { this.init() }

  ngOnInit() {
    this.route.queryParams.subscribe(item => {
      // console.log('item', item);
      // c chi tit  hin th ni dung chi tit ra
      this.refresh(item.id)
    });
  }

  init() {
    this.isMobile = this.apiCommons.isMobile();
    this.userInfo = this.mainService.getUserInfo();
  }

  // ly  tng theo id
  refresh(id) {
    this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-idea?id=' + id, true)
      .then(ideaDetail => {
        this.ideaInfo = ideaDetail
        this.refreshUserAction()
        // console.log(this.ideaInfo);
      })
      .catch(err => console.log('Li ly chi tit', err))
  }

  // Ly file cho  tng v comment
  // Kim tra userInfo ny  like, comment hoc chm im cha?
  refreshUserAction() {
    if (this.ideaInfo && this.ideaInfo.likes && this.ideaInfo.comments) {

      //Ly cc file cho  tng
      if (this.ideaInfo.idea && this.ideaInfo.idea.attach_id_list) {
        // thc hin truy vn ly danh sch file nh km
        this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-attach-files?id_list=' + JSON.stringify(this.ideaInfo.idea.attach_id_list), true)
          .then(list => {

            if (list && Array.isArray(list.images)) {
              this.ideaInfo.idea.images = list.images;
              this.ideaInfo.idea.images.forEach(file => file.src = this.apiAuth.serviceUrls.RESOURCE_SERVER + "/get-file-id?id=" + file.id)
            }

            if (list && Array.isArray(list.files)) {
              this.ideaInfo.idea.attachs = list.files;
            }

          })
          .catch(err => {
            console.log('Li ly file: ', err);
          });

      }
      // Kim tra this.userInfo ny  like  tng ny cha?
      this.ideaInfo.isUserVoted = this.ideaInfo.likes.findIndex(x => x.user_id === this.userInfo.id && x.activities_type > 0) >= 0
      // Kim tra this.userInfo ny  comment  tng ny cha?
      this.ideaInfo.isUserCommented = this.ideaInfo.comments.findIndex(x => x.user_id === this.userInfo.id) >= 0
      //Kim tra this.userInfo ny  chm im  tng ny cha?
      this.ideaInfo.isUserMarked = this.ideaInfo.marks.findIndex(x => x.user_id === this.userInfo.id) >= 0
      //Ly cc file cho cc bnh lun
      this.ideaInfo.comments.forEach(el => {
        if (el.attach_id_list) {
          // thc hin truy vn ly danh sch file nh km - tn file, kiu file, id  hin th ra
          this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-attach-files?id_list=' + JSON.stringify(el.attach_id_list), true)
            .then(list => {

              if (list && Array.isArray(list.images)) {
                el.images = list.images;
                el.images.forEach(file => file.src = this.apiAuth.serviceUrls.RESOURCE_SERVER + "/get-file-id?id=" + file.id)
              }

              if (list && Array.isArray(list.files)) {
                el.attachs = list.files;
              }

            })
            .catch(err => {
              console.log('Li ly file: ', err);
            });
        }
      })
    }
  }

  // focus n  comments
  focusCommentIdea() {
    this.textAreaElement.setFocus();
  }

  // Bm vo nt more 
  onClickMore(ev) {

    /* kim tra quyn ca userInfo m hin th menu khc nhau
    + nu user khng thuc  tng, th c quyn chm im cho  tng ny
    + nu l  tng thuc user th cho php sa ni dung, chuyn trng thi */

    // c th nh sau: Nu role l
    /*
      1	User thng	User  -- hin th mi mt menu chm im (nu khng phi  tng ca mnh)
                          -- Hoc menu sa  tng, chuyn trng thi (nu l  tng ca mnh)
      
      99	Developper	Ngi pht trin -- hin th ht menu
     */

    let settingsMenu = [];
    // menu y 
    // trng hp no th s xa b menu tng ng
    const allMenu = [
      // Cho tt c mi ngi tr userInfo==idea
      {
        id: 1
        , name: "Chm im  tng ny"
        , value: "MARK"
        , icon: {
          name: "microphone"
          , color: "warning"
        }
      }
      ,
      //Chnh sa  tng (cho user_id ca  tng )
      {
        id: 2
        , name: "Sa  tng ny"
        , value: "EDIT"
        , icon: {
          name: "create"
          , color: "primary"
        }
      }
      ,
      // ch cho admin 99, v user_id ca  tng trng vi n
      {
        id: 3
        , name: "Chuyn trng thi"
        , value: "CHANGE"
        , icon: {
          name: "hourglass"
          , color: "primary"
        }
      }
      ,
      // ch cho admin 99
      {
        id: 4
        , name: "Xa  tng ny"
        , value: "TRASH"
        , icon: {
          name: "trash"
          , color: "danger"
        }
      }
    ]

    if (this.userInfo && this.ideaInfo && this.ideaInfo.idea) {
      //user_id ca  tng trng vi id ca userInfo
      if (this.ideaInfo.idea.user_id === this.userInfo.id) {
        // cho php sa hoc chuyn trng thi
        settingsMenu = allMenu.filter(x => x.id === 2 || x.id === 3)
      } else {
        // ch cho php chm im
        settingsMenu = allMenu.filter(x => x.id === 1)
      }

      // Nu l admin th phn quyn nh sau:
      if (this.userInfo.role === 99) {
        // Nu l  tng ca admin th khng c quyn chm im
        if (this.ideaInfo.idea.user_id === this.userInfo.id) {
          settingsMenu = allMenu.filter(x => x.id !== 1)
        } else {
          // ton quyn
          settingsMenu = allMenu
        }
      }
    }

    this.apiCommons.presentPopover(
      ev, PopoverCardComponent
      , {
        type: 'single-choice',
        title: "Chn chc nng",
        color: "primary",
        menu: settingsMenu
      })
      .then(data => {
        this.processDetails(data);
      })
      .catch(err => {
        console.log('err: ', err);
      });
  }

  // Thc thi lnh khi chn nt more
  processDetails(data: any) {
    let cmd = data.value;
    // console.log('lenh', cmd);
    if (this.ideaInfo && this.ideaInfo.idea) {
      if (cmd === 'MARK') {
        // gi form chm im
        this.markIdea(this.ideaInfo.idea)
      } else if (cmd === 'EDIT') {
        //  sa  tng ny
        this.editIdea(this.ideaInfo.idea)
      } else if (cmd === 'CHANGE') {
        //  thay i trng thi  tng
        this.changeStatusIdea(this.ideaInfo.idea)
      } else if (cmd === 'TRASH') {
        //  loi b  tng ny
        this.trashIdea(this.ideaInfo.idea)
      }
    }
  }

  // Ngi dng bm nt like
  likeIdea(item) {
    this.apiAuth.postDynamicJson(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/like-idea', { id: item.id }, true)
      .then(idea => {
        // console.log(idea);
        this.ideaInfo = idea; // ly li ni dung ny
        this.refreshUserAction()
      })
      .catch(err => console.log(err))
  }

  // S kin ngi dng chn file  upload khi comment
  uploadFilesEvent(evt) {
    if (!evt.target || !evt.target.files || !evt.target.files.length) return
    for (let file of evt.target.files) {
      if (file.type.indexOf('image') >= 0) {
        file.isImage = true;
        const fr = new FileReader();
        fr.onloadend = () => {
          file.image = fr.result;
        };
        fr.readAsDataURL(file);
      }
      if (!this.uploadingFiles.find(x => x.name === file.name))
        this.uploadingFiles.push(file)
    }
    // console.log(this.uploadingFiles);
  }

  //Xa file  chn
  onClickRemoveFile(idx) {
    this.uploadingFiles.splice(idx, 1);
  }

  // hin th file hoc nh ra
  onClickViewItem(fileId) {
    this.iab.create(this.apiAuth.serviceUrls.RESOURCE_SERVER + "/get-file-id?id=" + fileId, `_system`);
  }

  // Gi ni dung comment i
  onClickSend() {
    if (this.message || this.uploadingFiles.length > 0) {

      this.apiCommons.showLoader('ang x l d liu trn my ch...')

      let form_data: FormData = new FormData();
      form_data.append("id", this.ideaInfo.idea.id);
      form_data.append("content", this.message ? this.message : this.uploadingFiles.length + ' file(s)');
      let i = 0;
      for (let file of this.uploadingFiles) {
        form_data.append('file_' + i++, file, file.filename);
      }
      this.apiAuth.postDynamicFormData(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/comment-idea'
        , form_data, true)
        .then(idea => {
          this.apiCommons.hideLoader();
          this.ideaInfo = idea; // ly li ni dung ny
          this.refreshUserAction()
        })
        .catch(err => {
          this.apiCommons.hideLoader();
          // console.log('Li: ', err);
        });

      this.message = '';
      this.uploadingFiles = [];
    }
  }

  // chm im  tng ny theo cc tiu ch nh ngha
  async markIdea(idea) {

    if (this.ideaInfo.idea.user_id === this.userInfo.id) {
      this.apiCommons.showToast('Bn khng t chm im cho mnh c!', 2000, 'warning', 'middle')
      return
    }

    let questions;
    let userMarkIdea;
    try {
      questions = await this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-questions', true)
      userMarkIdea = await this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/user-mark-idea?id=' + idea.id, true)
    } catch{ }

    // Ly li im  chm trc 
    let arrayTestDemo = [];
    for (let ques of questions) {
      let oldMarkQues = userMarkIdea.find(x => x.question_id === ques.id);
      let oldMark = oldMarkQues ? oldMarkQues.point : 0; // ly li im  chm trc 
      let obj = {
        type: "range-text",
        key: "question_" + ques.id,
        name: ques.question,
        value: oldMark,
        min: ques.min_point,
        max: ques.max_point
      }
      arrayTestDemo.push(obj);
    }

    // Chm im  tng - popup ca s chm im
    let form: any = {
      title: 'Chm im  tng'
      , buttons: [
        { color: 'danger', icon: 'close', next: 'CLOSE' }
      ]
      , items: [
        { type: 'title', name: idea.title, key: 'id', value: idea.id }
        , ...arrayTestDemo // s dng spread operation  y  load ng cc questions chm im
        , {
          type: 'button'
          , options: [
            { name: "Reset", next: "RESET" }
            , {
              name: 'Gi chm im'
              , next: 'CALLBACK'
              , id: idea.id
              , url: this.apiAuth.serviceUrls.RESOURCE_SERVER + '/mark-idea'
              , token: true
              , command: 'MARK'
            }
          ]
        }
      ]
    }

    this.apiCommons.openModal(DynamicFormMobilePage,
      {
        parent: this,
        callback: this.callbackProcess,
        form: form
      }
    );

  }

  // sa li  tng ny
  async editIdea(idea) {
    // popup ca s ny ln v cho php chnh sa  tng ny
    let parameters;
    try {
      parameters = await this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-idea-parameters', true)
    } catch{ }

    let categoryOptions = parameters && parameters.ideas_categories ? parameters.ideas_categories : [];

    let statusOptions = parameters && parameters.ideas_statuses ? parameters.ideas_statuses : [];

    // Chnh sa  tng - popup ca s chnh sa
    let form: any = {
      title: 'Sa  tng'
      , buttons: [
        { color: 'danger', icon: 'close', next: 'CLOSE' }
      ]
      ,
      items: [
        // Danh sch cc trng nhp liu
        { type: "hidden", key: "id", value: idea.id }
        , { type: "text", key: "title", value: idea.title, name: "Ch  l g? ", hint: "Nhp ch  ca  tng ny t 5-200 k t", input_type: "text", icon: "md-help", validators: [{ required: true, min: 1, max: 200 }] }
        , { type: "text_area", key: "description", value: idea.description, name: "M t ni dung  tng ca bn t 50 n 1000 t", hint: "Nhp m t  tng ca bn", input_type: "text", icon: "md-information-circle", validators: [{ required: true, min: 1 }] }
        , { type: "select", key: "category_id", value: "" + idea.category_id, name: "Phn loi  tng?", icon: "contrast", options: categoryOptions, color: "warning" }
        , { type: "select", key: "status", value: "" + idea.status, name: "Trng thi ca  tng?", icon: "clock", options: statusOptions, color: "secondary" }
        ,
        {
          type: 'button'
          , options: [
            { name: "Reset", next: "RESET" }
            , {
              name: 'Gi sa  tng'
              , id: idea.id              // tr li id ca  tng ny
              , next: 'CALLBACK'
              , url: this.apiAuth.serviceUrls.RESOURCE_SERVER + '/edit-idea', type: "FORM-DATA", token: true
              , command: 'EDIT'
            }
          ]
        }
      ]
    }

    this.apiCommons.openModal(DynamicFormMobilePage,
      {
        parent: this,
        callback: this.callbackProcess,
        form: form
      }
    );
  }

  // Chuyn trng thi ca  tng
  async changeStatusIdea(idea) {
    let parameters;
    try {
      parameters = await this.apiAuth.getDynamicUrl(this.apiAuth.serviceUrls.RESOURCE_SERVER + '/get-idea-parameters', true)
    } catch{ }

    let statusOptions = parameters && parameters.ideas_statuses ? parameters.ideas_statuses : [];

    let form: any = {
      title: 'Thay i trng thi'
      , buttons: [
        { color: 'danger', icon: 'close', next: 'CLOSE' }
      ]
      ,
      items: [
        // Danh sch cc trng nhp liu
        { type: "hidden", key: "id", value: idea.id }
        , { type: "select", key: "status", value: "" + idea.status, name: "Trng thi ca  tng?", icon: "clock", options: statusOptions, color: "warning" }
        ,
        {
          type: 'button'
          , options: [
            {
              name: 'Chuyn trng thi  tng ny'
              , id: idea.id              // tr li id ca  tng ny
              , next: 'CALLBACK'
              , url: this.apiAuth.serviceUrls.RESOURCE_SERVER + '/edit-idea', type: "FORM-DATA", token: true
              , command: 'EDIT'
            }
          ]
        }
      ]
    }

    this.apiCommons.openModal(DynamicFormMobilePage,
      {
        parent: this,
        callback: this.callbackProcess,
        form: form
      }
    );
  }

  // loi b  tng ny
  trashIdea(idea) {
    let form: any = {
      title: 'Dng  tng'
      , buttons: [
        { color: 'danger', icon: 'close', next: 'CLOSE' }
      ]
      ,
      items: [
        // Danh sch cc trng nhp liu
        { type: "hidden", key: "id", value: idea.id }
        , { type: "select", key: "status", value: "0", name: "Trng thi ca  tng?", icon: "clock", options: [{ value: "0", name: "Trin khai sau" }], color: "secondary" }
        ,
        {
          type: 'button'
          , options: [
            {
              name: 'Dng  tng ny'
              , id: idea.id
              , next: 'CALLBACK'
              , url: this.apiAuth.serviceUrls.RESOURCE_SERVER + '/edit-idea', type: "FORM-DATA", token: true
              , command: 'EDIT'
            }
          ]
        }
      ]
    }

    this.apiCommons.openModal(DynamicFormMobilePage,
      {
        parent: this,
        callback: this.callbackProcess,
        form: form
      }
    );
  }

  // hm gi li x l form popup
  callbackProcess = function (res) {
    return new Promise<any>((resolve, reject) => {

      // console.log(res);

      if (res.error) {
        this.apiCommons.presentAlert('Error:<br>' + (res.message ? res.message : "Error Unknow: " + JSON.stringify(res.error, null, 2)));
      } else if (res.response_data) {
        if (res.button.command === "MARK") {
          this.refresh(res.button.id);
        }
        if (res.button.command === "EDIT") {
          this.refresh(res.button.id)
        }

      }

      resolve({ next: "CLOSE" });

    });
  }.bind(this);

}
